async function loadData() {
	let response = await fetch("https://www.cbr-xml-daily.ru/daily_json.js"),
        json = await response.json(),
   
        select = document.querySelector('.select').value,
        curencyUSD = JSON.stringify(json.Valute.USD.Value),
        curencyEUR = JSON.stringify(json.Valute.EUR.Value),
        date = JSON.stringify(json.Date),
        input = document.querySelector(".searchTxt").value;

    document.querySelector(".date").innerHTML = date
    if(select === '0') {document.querySelector(".summer").value = input * curencyUSD}
    if(select === '1') {document.querySelector(".summer").value = input * curencyEUR} }

export default loadData