import React from 'react'

const Comp = ({loadData}) => {
    loadData()
    return(
        <div>
            <h1>Конвертер валют</h1>
            <div className="date">Дата курса валюты:</div>
        <input type="number"  className="searchTxt"  defaultValue="1" name="searchTxt"  onKeyUp={loadData} onClick={loadData}/><br/>
        <span >=</span><br/>
        <input type="text" name="" className="summer" placeholder={"0"}/>
            <select className="select" onChange={loadData}>
                <option value="0">USD</option>
                <option value="1">EURO</option>
            </select>
         <div className="selectRu">/ RU</div>
        </div>
    )
}

export default Comp